
.myBoards {
	width: 70%;
	margin: auto;
	text-align: center;
  margin-top: 3em;
}
.myBoards .createButton {
    padding: 1em;
    border: none;
    background: #14a0c5;
    color: white;
    font-family: Panama;
    font-size: 1.5em;
    font-weight: bold;
    margin-bottom: 1em;
    cursor: pointer;
}
.myBoards .createButton:hover {
	background-color: #08627a;
}
.myBoards .button {
	background-color: rgb(20, 160, 197);
	padding: 1em;
	cursor: pointer;
}
.myBoards .button:hover {
	background-color: #08627a;
}
.myBoards h2 {
  padding: 1em;
	margin: 1em;
}
.listBoards {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.miniBoard {
	margin: 1em;
	width: 20em;
	height: 20em;
	background-color: rgb(20, 160, 197);
	cursor: pointer;
}
.miniBoard:hover {
	background-color: #fff133;
}




